import React, {useState} from 'react';
import styled from "styled-components";
import {ReactComponent as User} from "../../resource/icon/user.svg";
import {Colors} from "../../resource/Colors";
import FormInput from "../common/FormInput";
import Button from "../common/Button";
import {UserApi} from "../../api/app/UserApi";
import * as Swal from '../../api/common/alert';

const Container = styled.div`
  width: 50%;
  height: 100%;
  border-radius: 10px;
  border: 1px solid #D9D9D9;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
`;

const Title = styled.p`
  font-size: 24px;
  font-weight: 800;
  color: ${Colors.primaryColor};
`;

const UserRegisterForm = () => {
  const [name, setName] = useState<string>('');
  const [age, setAge] = useState<string>('');

  const handleClick = () => {

    if (!name.trim() || !age.trim()) { // trim()   
        Swal.alert('   .', '', 'warning');
        return;
    }

    //   : ()   
    const nameRegex = /^[a-zA-Z-]+[0-9]*$/;
    if (!nameRegex.test(name)) {
        Swal.alert('     ,    .\n,    .', '', 'warning');
        return;
    }

    if (isNaN(parseInt(age))) { // isNaN:     (   true )
        Swal.alert('   .', '','warning');
        return;
    }

    UserApi.postUser(name, isNaN(parseInt(age)) ? null : parseInt(age))
      .then(data => {
        Swal.alert(' !', '', 'success');
        setName('');
        setAge('');
      })
      .catch(error => {
        //      
        const errorMessage = error?.data || error.message || ' .';
        Swal.alert(errorMessage, '', 'error');
      })
  }

  return (
    <Container>
      <User />
      <Title> </Title>
      <FormInput title={''} value={name} onChange={setName} />
      <FormInput title={''} value={age} onChange={setAge} />
      <Button label={''} onClick={handleClick} />
    </Container>
  );
};

export default UserRegisterForm;