import React, {useState} from 'react';
import styled from "styled-components";
import {ReactComponent as Book} from "../../resource/icon/book.svg";
import {Colors} from "../../resource/Colors";
import FormInput from "../common/FormInput";
import Button from "../common/Button";
import {BookApi} from "../../api/app/BookApi";
import * as Swal from '../../api/common/alert';

const Container = styled.div`
  width: 50%;
  height: 100%;
  border-radius: 10px;
  border: 1px solid #D9D9D9;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
`;

const Title = styled.p`
  font-size: 24px;
  font-weight: 800;
  color: ${Colors.primaryColor};
`;

const BookAdditionForm = () => {
  const [bookTitle, setBookTitle] = useState<string>('');

  const handleClick = () => {

    if (!bookTitle.trim()) { // trim()   
        Swal.alert('   .', '', 'warning');
        return;
    }

    BookApi.postBook(bookTitle)
      .then(data => {
        Swal.alert('  !', '', 'success');
        setBookTitle('');
      })
      .catch(error => {
        //      
        const errorMessage = error?.data || error.message || ' .';
        Swal.alert(errorMessage, '', 'error');
      })
  }

  return (
    <Container>
      <Book />
      <Title> </Title>
      <FormInput title={' '} value={bookTitle} onChange={setBookTitle} />
      <Button label={''} onClick={handleClick} />
    </Container>
  );
};

export default BookAdditionForm;