import React, { createContext, useEffect, useState } from 'react';
import api from '../api/login/api';
import Cookies from 'js-cookie';
import * as auth from '../api/login/auth';
import { useNavigate } from 'react-router-dom'
import * as Swal from '../api/common/alert';

export const LoginContext = createContext(); // LoginContext    
                                             // LoginContextProvider   ,   useContext(LoginContext)   
LoginContext.displayName = 'LoginContextName' // displayName:      ( Context )

/**
 *  (isLogin) ,  (, ,  )   
 *  (isLogin)  (logout)   
 * -   ( ,  )
 * - 
 * - 
 */
const LoginContextProvider = ({children}) => {

    /**
     * 
     * -  ()
     * -  
     * -  
     * -   ()
     */

    /*----------------------------[State]--------------------------- */
    //  ()
    const [isLogin, setLogin] = useState(false); //   (false)

    //  
    const [adminInfo, setAdminInfo] = useState({});

    //  
    const [authorities, setAuthorities] = useState({isUser : false, isAdmin : false});

    //   
    const [rememberEmail, setRememberEmail] = useState();

    //     (true:   / false:  )
    const [isLoading, setIsLoading] = useState(true);
    /*-------------------------------------------------------------- */

    //  
    const navigate = useNavigate();

    /**
     *  () 
     * -  jwt  
     * - jwt   
     * -
     */
    const loginCheck = async () => { // async ():   
        setIsLoading(true); //    
        
        const accessToken = Cookies.get("accessToken");

        if(!accessToken) {
            logoutSetting(); // JWT   
            setIsLoading(false); //    
            return;
        }

        /**
         *  accessToken(jwt)    jwt 
         * - axios  : api    
         *   1) api.defaults.headers.common: axios   api    
         *   2) Authorization: Authorization  
         *   3) controller  AuthenticationPrincipal   JWT    
         */
        api.defaults.headers.common.Authorization = `Bearer ${accessToken}`

        //   
        try {
            const response = await auth.info(); // auth.info(): api    , api   JWT  
            const data = response.data;

            if(data === 'UNAUTHRIZED' || response.status === 401) {  //      
                logoutSetting();
            } else { //     
                loginSetting(data, accessToken);
            }
        } catch (error) {
            console.log(`error : ${error}`);
            logoutSetting();
        } finally {
            setIsLoading(false); //    
        }
    }

    // 
    const login = async(email, password) => {

        try {
            const response = await auth.login(email, password);
            const data = response.data;
            const status = response.status;
            const headers = response.headers;
            const authorization = headers.authorization;
            const accessToken = authorization.replace("Bearer ", ""); // jwt

            //  
            if(status === 200) {
                //  accessToken(jwt) 
                Cookies.set("accessToken", accessToken);

                //   (/users/{email} => userData)
                loginCheck();

                navigate("/mainPage");

            }
        } catch (error) {
            //  
             Swal.alert(` `, "    .", "error");
        }

    }

    // (: force=false)
    const logout = (force=false) => {

        if( force ) { // force=true   ( X) ex)  ,        
            //  
            logoutSetting();

            //    "/" ()
            navigate("/");
            return
        }

        // force=false   ( O)
        Swal.confirm("?", " .", "question", (result) => {
            if( result.isConfirmed ) {
                //  
                logoutSetting();

                //   
                navigate("/");
            }
        });

    }

    //  
    // adminData( ), accessToken(jwt)
    const loginSetting = (adminData, accessToken) => {

        //    
        const {id, email, name, authorities} = adminData;

        // 
        // const roleList = authorities.map((auth) => auth.authorities);

        // 
        // console.log(`roleList : ${roleList}`);

        // axios   - Authorization : `Bearer ${accessToken}`
        api.defaults.headers.common.Authorization = `Bearer ${accessToken}`;

        //   true 
        setLogin(true);

        //    (name )
        setAdminInfo({ id, email, name, authorities });

        //   
        const updatedAuthorities = {isUser : false, isAdmin : false};
        authorities.forEach((role) => {
            if(role === 'ROLE_USER') updatedAuthorities.isUser = true;
            if(role === 'ROLE_ADMIN') updatedAuthorities.isAdmin = true;
        });
        setAuthorities(updatedAuthorities);
    }


    //  
    const logoutSetting = () => {

        // axios  
        api.defaults.headers.common.Authorization = undefined;

        //  
        Cookies.remove("accessToken");

        //   : false
        setLogin(false);

        //   
        setAdminInfo(null);

        //   
        setAuthorities(null);

    }


    // useEffect: React      Hook
    useEffect( () => {
        //  
        loginCheck();
    }, []);

    return (
        // LoginContext.Provider    Context 
        // LoginContextProvider   Context    
        <LoginContext.Provider value={{ isLogin, adminInfo, authorities, login, loginCheck, logout, isLoading, setAdminInfo }}>
            {children}  
        </LoginContext.Provider>
    )
}

export default LoginContextProvider