import React, {useContext} from 'react';
import MainPage from "./page/MainPage";
import { BrowserRouter, Navigate, Route, Routes } from 'react-router-dom';
import LoginContextProvider from './context/LoginContextProvider';
import { LoginContext } from './context/LoginContextProvider'
import Login from './page/login/Login';
import Join from './page/join/Join';
import * as Swal from './api/common/alert';

/**
 * PublicRoute ProtectedRoute      
 * PublicRoute:      
 * ProtectedRoute:      
 */
const PublicRoute = ({ children }: { children: JSX.Element }) => { // { children }: PublicRoute    
                                                                   // { children: JSX.Element }: children  JSX.Element 
  const { isLogin, isLoading } = useContext(LoginContext);
  
  if (isLoading) return null; //        
  
  return !isLogin ? children : <Navigate to="/mainPage" replace />; // isLogin true : "/main" , isLogin false :   
};

const ProtectedRoute = ({ children, adminOnly }: { children: JSX.Element, adminOnly?: boolean }) => {
  const { isLogin, isLoading, authorities } = useContext(LoginContext);
  
  if (isLoading) return null;

  if (!isLogin) return <Navigate to="/loginPage" replace />;

  //     
  if (adminOnly && (!authorities || !authorities.isAdmin)) {
    Swal.alert("   .", "", "warning");
    return <Navigate to="/mainPage" replace />;
  }

  return children;

  // return isLogin ? children : <Navigate to="/loginPage" replace />; // isLogin true :   , isLogin false : "/login" 

};

/**
 * App :     
 * <LoginContextProvider>:     
 * <AppRoutes />:    , React Router Routes Route    
 */
function App() {
  return (
    <LoginContextProvider>
      <AppRoutes />
    </LoginContextProvider>
  );
}

//     
function AppRoutes() {
  const { isLoading } = useContext(LoginContext);

  if (isLoading) return null; //       

  /**
   * <Routes>: Route  (  )
   * <Route path="..." element={...} />:  (path)   
   */
  return (
    <Routes>

      <Route path="/" 
        element={
          <ProtectedRoute>
            <MainPage />
          </ProtectedRoute>
        } 
      />
      
      <Route path="/loginPage"
        element={
          <PublicRoute>
            <Login />
          </PublicRoute>
        }
      />

      <Route path="/joinPage"
        element={
          <PublicRoute>
            <Join />
          </PublicRoute>
        }
      />

      <Route path="/mainPage"
        element={
          <ProtectedRoute>
            <MainPage />
          </ProtectedRoute>
        }
      />

      <Route path="/userPage"
        element={
          <ProtectedRoute adminOnly={true}>
            <MainPage />
          </ProtectedRoute>
        }
      />

      <Route path="/adminPage"
        element={
          <ProtectedRoute>
            <MainPage />
          </ProtectedRoute>
        }
      />


    </Routes>
  );
}

export default App;
