import React, { useContext } from 'react'
import { useNavigate } from 'react-router-dom'; // 
import './LoginForm.css';
import { LoginContext } from '../../../context/LoginContextProvider'

const LoginForm = () => {

    const navigate = useNavigate();

    const {login} = useContext(LoginContext); // LoginContextProvider  LoginContext login  

    const onLogin = (e) => { //  form submit onLogin  
        e.preventDefault(); //       <submit>     , <a>     

        const form = e.target; //   e  form    
        const email = form.email.value;
        const password = form.password.value;

        //  : LoginContext login  
        login(email, password);
    }

    return (
        <div className="form">
            <h2 className='login-title'></h2>

            <form className="login-form" onSubmit={(e) => onLogin(e)}>
                <div>
                    <label htmlFor="email"></label>
                    <input type="text" 
                           id='email' 
                           placeholder=' ' 
                           name='email' 
                           autoComplete='email' 
                           required 
                           // TODO:      
                           // FIXME: rememberEmail     
                           // defaultValue={}
                    />
                </div>
                <div>
                    <label htmlFor="password"></label>
                    <input type="password"
                           id='password'
                           placeholder=' '
                           name='password'
                           autoComplete='password'
                           required       
                    />
                </div>
                <button type='submit' className='btn btn--form btn-login'></button>
                <hr className='login-hr'/>
                <button className="btn btn--form btn-join" onClick={() => navigate('/joinPage')}></button>
            </form>
            
        </div>
    )
}

export default LoginForm