import React from 'react'
import JoinForm from '../../component/form/join/JoinForm'
import * as auth from '../../api/login/auth'
import { useNavigate } from 'react-router-dom'
import * as Swal from '../../api/common/alert';

const Join = () => {

  const navigate = useNavigate(); // useNavigate: React Router    

  //   
  const join = async (form) => {  // form:      ex) { email, name, password }

    let response; //  API  
    let data;     //  API  : SUCCESS or FAIL

    try {
        response = await auth.join(form);  //  (POST )
    } catch (error) {
        const errorMessage = error.response.data;
        Swal.alert(errorMessage, '', 'error');
        return; //     
    }

    data = response.data; //   
    const status = response.status; // HTTP   

    if(status === 200) {
        Swal.alert(` !`, '', 'success');
        navigate("/loginPage"); //      
    } else {
        Swal.alert(` .`, '', 'error')
    }

  }

  return (
    // Header  (JoinForm)   
    // join={join}: {join}  join  JoinForm 
    <>
        <div className="container">
          <JoinForm join={join} />
        </div>
    </>
  )

}

export default Join