import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { BrowserRouter } from 'react-router-dom';

/**
 * index.tsx: React  
 * ReactDOM.createRoot():   DOM  React  DOM , React Root 
 */
const root = ReactDOM.createRoot(document.getElementById('root') as HTMLElement); //  DOM id="root"  , React    

/**
 * .render(): React   DOM ,         DOM  <div id="root"></div>  
 * <React.StrictMode>:   ,     
 * <BrowserRouter>:  ,  URL ,      (<App />  Routes Route   URL   )
 * <App />:  ,   src/App.tsx 
 */

root.render(
  <React.StrictMode>
    <BrowserRouter future={{ v7_startTransition: true, v7_relativeSplatPath: true }}>
      <App />
    </BrowserRouter>
  </React.StrictMode>
);

reportWebVitals();  // reportWebVitals(): React     
