import React, {useState} from 'react';
import styled from "styled-components";
import MainTopBar from "../component/MainTopBar";
import FormPage from "./form/FormPage";
import UserListPage from "./user-list/UserListPage";
import Admin from "./admin/Admin"; //   
import { useLocation, useNavigate } from "react-router-dom";

const Container = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  min-width: 1440px;
`

const ContentsContainer = styled.div`
  width: 100%;
  height: calc(100% - 60px);
  box-sizing: border-box;
  overflow-y: auto;
`;

// Tab 
export enum Tab {
   FORM = "mainPage", // Tab.FORM = "main"
   USER = "userPage", // Tab.USER = "user"
   ADMIN = "adminPage", // Tab.ADMIN = "admin"
}

const MainPage = () => {

  const location = useLocation(); //  URL  
  const navigate = useNavigate();

  // URL   
  const currentTab = location.pathname.replace("/", "") as Tab; //  URL "/"   Tab   currentTab = main/user/admin
  const tab = Object.values(Tab).includes(currentTab) ? currentTab : Tab.FORM; // currentTab Tab  currentTab ,   Tab.FORM() 

  //    URL 
  const setTab = (newTab: Tab) => {  // (newTab: Tab) - newTab  Tab 
    navigate(`/${newTab}`);
  };

  return (
    <Container>
      <MainTopBar setTab={setTab} tab={tab}/>
      <ContentsContainer>
        {tab === Tab.FORM && <FormPage />} {/* tab === Tab.FORM true  => <FormPage />  */}
        {tab === Tab.USER && <UserListPage />}
        {tab === Tab.ADMIN && <Admin />}
      </ContentsContainer>
    </Container>
  );
};

export default MainPage;