import React, { useEffect, useState, useRef } from 'react'
import { useNavigate } from 'react-router-dom'
import '../login/LoginForm.css';
import * as Swal from '../../../api/common/alert';

const JoinForm = ({ join }) => {

  const navigate = useNavigate();
  const [isRoleOpen, setIsRoleOpen] = useState(false);
  const [selectedRole, setSelectedRole] = useState('ROLE_ADMIN');
  const roleRef = useRef(null);

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (roleRef.current && !roleRef.current.contains(event.target)) {
        setIsRoleOpen(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, []);

  const onJoin = (e) => {

      e.preventDefault(); // submit   

      const form = e.target;
      const email = form.email.value.trim();
      const name = form.name.value.trim();
      const password = form.password.value;
      const emailVerified = form.emailVerified.value;

      //   
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      if (!emailRegex.test(email)) {
            Swal.alert("   .", "", "warning");
            return;
      }

      //   
      if(!email || !name || !password) {
        Swal.alert('   .', '', 'warning')
        return;
      }

      //   
      if (!selectedRole) {
          Swal.alert(" .", "", "warning");
          return;
      }

      join({ email, name, password, emailVerified, authorities: selectedRole });
  }

  return (
    <div className="form">
      <h2 className='login-title'></h2>

      <form className="login-form" onSubmit={(e) => onJoin(e)}>
          <div>
              <label htmlFor="email"></label>
              <input type="text" 
                    id='email' 
                    placeholder=' ' 
                    name='email' 
                    autoComplete='email' 
                    required 
              />
          </div>

          <div>
            <label htmlFor="name"></label>
                <input type="text" 
                      id='name'
                      placeholder=' ' 
                      name='name'
                      autoComplete='name'
                      required 
                />
            </div>

            <div>
              <label htmlFor="password"></label>
              <input type="password"
                    id='password'
                    placeholder=' '
                    name='password'
                    autoComplete='password'
                    required       
              />
            </div>

            {/*     */}

            <div className="custom-select" ref={roleRef}>
                <label htmlFor="role"></label>

                <div
                    className={`select-selected ${isRoleOpen ? 'select-arrow-active' : ''}`}
                    onClick={() => setIsRoleOpen(!isRoleOpen)}
                >
                    {selectedRole === 'ROLE_ADMIN' ? '' : ''}
                </div>

                {isRoleOpen && (
                    <div className="select-items">
                        <div
                            className={selectedRole === 'ROLE_ADMIN' ? 'same-as-selected' : ''}
                            onClick={() => {
                                setSelectedRole('ROLE_ADMIN');
                                setIsRoleOpen(false);
                            }}
                        >
                            
                        </div>

                        <div
                            className={selectedRole === 'ROLE_USER' ? 'same-as-selected' : ''}
                            onClick={() => {
                                setSelectedRole('ROLE_USER');
                                setIsRoleOpen(false);
                            }}
                        >
                            
                        </div>
                    </div>
                )}
            </div>

            <input
                type="hidden"
                name="emailVerified"
                value="true"
            />
          <div className="button-group">
            <button type='submit' className='btn btn--form btn-join'></button>
            <button 
              type='button' 
              className='btn btn--form btn-cancel' 
              onClick={() => navigate("/loginPage")}
            >
              
            </button>
          </div>
      </form>

    </div>
  )
}

export default JoinForm