import React, { useEffect, useState, useRef } from 'react'
import { useNavigate } from 'react-router-dom'
import './AdminForm.css';
import * as Swal from '../../../api/common/alert';

const AdminForm = ({ adminInfo, updateAdmin, deleteAdmin }) => {

  const navigate = useNavigate();
  const [isRoleOpen, setIsRoleOpen] = useState(false); // isRoleOpen:       (true: /false: )
  const [selectedRole, setSelectedRole] = useState(adminInfo?.authorities.includes("ROLE_ADMIN") ? 'ROLE_ADMIN' : 'ROLE_USER');
  const roleRef = useRef(null); //       ref

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (roleRef.current && !roleRef.current.contains(event.target)) { // roleRef.current: ( )
        setIsRoleOpen(false); //      
      }
    };

    document.addEventListener('mousedown', handleClickOutside); //    handleClickOutside    
    return () => document.removeEventListener('mousedown', handleClickOutside); //  (unmount)  
  }, []);

  const onUpdate = (e) => {

      e.preventDefault(); // submit   

      const form = e.target;
      const email = form.email.value.trim();
      const name = form.name.value.trim();
      const password = form.password.value;
      const emailVerified = form.emailVerified.value;

      //   
      if(!email || !name || !password) {
        Swal.alert('   .', '', 'warning')
        return;
      }

      //   
      if (!selectedRole) {
          Swal.alert(" .", "", "warning");
          return;
      }

      updateAdmin({ email: adminInfo.email, name, password, emailVerified, authorities: selectedRole });
  }

  return (
    <div className="admin-form">
      <h2 className='admin-title'>  </h2>

        <form className="login-form" onSubmit={(e) => onUpdate(e)}>
          <div>
            <label htmlFor="email"></label>
            <input type="text" 
                  id='email' 
                  placeholder=' ' 
                  name='email' 
                  autoComplete='email' 
                  readOnly
                  defaultValue={ adminInfo?.email }
                  className="readonly-input"
                  style={{ backgroundColor: 'lightgray' }}
            />
          </div>

          <div>
            <label htmlFor="name"></label>
            <input type="text" 
                  id='name'
                  placeholder=' ' 
                  name='name'
                  autoComplete='name'
                  required 
                  defaultValue={ adminInfo?.name }
            />
          </div>

          <div>
            <label htmlFor="password"></label>
            <input type="password"
                  id='password'
                  placeholder=' '
                  name='password'
                  autoComplete='password'
                  required       
            />
          </div>

          <div className="custom-select" ref={roleRef}>
            <label htmlFor="role"></label>

            <div
              className={`select-selected ${isRoleOpen ? 'select-arrow-active' : ''}`}
              onClick={() => setIsRoleOpen(!isRoleOpen)}
            >
              {selectedRole === 'ROLE_ADMIN' ? '' : ''}
            </div>

            {isRoleOpen && (  // isRoleOpen true     false    
              <div className="select-items">
                <div
                  className={selectedRole === 'ROLE_ADMIN' ? 'same-as-selected' : ''}
                  onClick={() => {
                    setSelectedRole('ROLE_ADMIN');
                    setIsRoleOpen(false);
                  }}
                >
                  
                </div>

                <div
                  className={selectedRole === 'ROLE_USER' ? 'same-as-selected' : ''}
                  onClick={() => {
                    setSelectedRole('ROLE_USER');
                    setIsRoleOpen(false);
                  }}
                >
                  
                </div>
              </div>
            )}
          </div>

            {/*     */}
            <input
                type="hidden"
                name="emailVerified"
                value="true"
            />
        <button type='submit' className='btn btn--form btn-join'> </button>
        <hr className='login-hr'/>
        <button type='button' className="btn btn--form btn-login" onClick={ () => deleteAdmin(adminInfo.email) }> </button>
      </form>

    </div>
  )
}

export default AdminForm